"use client";
import React from "react";

type Props = {
  prompt: string;
  onChange: (v: string) => void;
  onSubmit: () => void;
  disabled?: boolean;
};

export default function PromptInputBar({ prompt, onChange, onSubmit, disabled }: Props) {
  return (
    <div className="sticky top-0 z-30 border-b bg-white/90 backdrop-blur supports-[backdrop-filter]:bg-white/70">
      <div className="mx-auto max-w-5xl p-4">
        <div className="flex flex-col gap-3 sm:flex-row sm:items-start">
          <textarea
            value={prompt}
            onChange={(e) => onChange(e.target.value)}
            placeholder="Enter a prompt..."
            className="w-full resize-none min-h-[80px] rounded-lg p-4 border border-gray-300 shadow-sm focus:outline-none focus:ring-2 focus:ring-black/20"
          />
          <div className="flex w-full shrink-0 items-stretch gap-2 sm:w-auto sm:flex-col">
            <button
              onClick={onSubmit}
              disabled={disabled}
              className="inline-flex items-center justify-center rounded-lg bg-black px-4 py-2 text-white transition-all duration-200 hover:bg-black/90 disabled:opacity-60"
            >
              Submit Prompt
            </button>
            <button
              type="button"
              className="inline-flex items-center justify-center rounded-lg border px-4 py-2 text-gray-800 transition-all duration-200 hover:bg-gray-100"
            >
              Prompt Boost
            </button>
            <button
              type="button"
              className="inline-flex items-center justify-center rounded-lg border px-4 py-2 text-gray-800 transition-all duration-200 hover:bg-gray-100"
            >
              Attach File
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
